@import url(https://fonts.googleapis.com/css2?family=Urbanist:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,800;1,400;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Acme&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500&display=swap);
.header{top:0;z-index:9999;position:fixed;font-family:Roboto, Arial, Helvetica, "Droid Sans", sans-serif;color:tan;background-color:#000707;font-size:1.1rem;font-weight:600;transition:top ease 0.3s}.logo{margin-right:2rem}img{width:100%;object-fit:contain}a{text-decoration:none;color:currentColor;cursor:pointer}a:hover{color:tan;-webkit-filter:brightness(1.2);filter:brightness(1.2)}li{list-style:none}ul{margin:0;padding:0}.account{font-size:1.3rem}.avatar{width:45px}.avatar img{border-radius:50%}.avatar:hover{-webkit-filter:brightness(1.2);filter:brightness(1.2);cursor:pointer}.logo{cursor:pointer}.logo:hover .logo_dropdown_icon path{fill:#fff}.riot_products{z-index:9998;color:#333333;background-color:#fff;width:calc(100vw - 1.1rem);height:calc(100vh - 200px);overflow-y:scroll;left:-1rem;font-family:"Raleway", sans-serif;font-weight:900;font-size:1.8rem;transition:all 1 ease-in}@media screen and (max-width: 765px){.riot_products{width:calc(80vw - 1rem)}}.game_poster{padding:0.25rem;height:100%;height:inherit}.game_poster_big{padding:0.25rem;height:inherit}@media screen and (max-width: 576px){.game_poster{width:50%;padding:0.25rem;height:100%;height:inherit}.game_poster_big{width:100%;padding:0.25rem;height:inherit}}

.img_big:hover,.img_sm:hover{box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22)}.img_big img,.img_sm img{border-radius:5px}.riot_games_content h6{color:#7e7e7e;font-size:0.7rem;font-weight:800;line-height:1rem}.game_poster_big,.game_poster{display:inline-block}

.footer{background-color:#252423;color:#e7e6e3;font-weight:bold;font-size:0.8rem}.social_contact{display:flex;font-size:1.1rem;flex-wrap:nowrap}.logo{margin-right:0}

.banner{background-image:url("https://www.riotgames.com/darkroom/800/d16bfdda8ad27287d56de3bb230aed0d:4839a3501847cb964cb61d062ee8d0f5/tibbers.jpg")}@media (min-width: 600px){.banner{background-image:url("https://www.riotgames.com/darkroom/1000/d16bfdda8ad27287d56de3bb230aed0d:ff467e666846eb499caed1d29ef8507a/tibbers.jpg")}}@media (min-width: 1000px){.banner{background-image:url("https://www.riotgames.com/darkroom/2000/e76c532a5193f5ca58776f2bae5c7b56:9ad0db47f1d7fb8ee5e4a3843d63a96b/tibbers.jpg")}}.banner{width:calc(100vw - 0);height:calc(100vh - 70px);background-position:50% 50%;background-repeat:no-repeat;background-size:cover}.hasagi_intro{color:#fff;font-weight:bold;left:50%;text-align:center;font-size:3rem;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

button{background-color:transparent;position:absolute;bottom:10%;left:10%;opacity:0;-webkit-transform:ease 0.3;transform:ease 0.3;border-radius:32px 30px 30px 32px;border:1px solid #fff;font-size:1rem;font-weight:600;letter-spacing:1px;line-height:2rem}button:hover{color:#d30202;-webkit-filter:brightness(1.2);filter:brightness(1.2);background-color:#fff}.riot_game_item{cursor:pointer;transition:all ease 0.3s}.riot_game_item:hover .game_item{opacity:0.7}.riot_game_item:hover .game_item img{-webkit-transform:scale(1.2);transform:scale(1.2);-webkit-filter:brightness(0.8);filter:brightness(0.8)}.riot_game_item:hover button{opacity:1}.game_item{overflow:hidden;transition:all ease 0.3s}.game_item img{border-radius:5px;transition:all ease 0.3s}

.notif{background:#171616;background:linear-gradient(90deg, rgba(23,22,22,0) 0%, rgba(23,22,22,0.3) 7%, rgba(23,22,22,0.65) 15%, rgba(23,22,22,0.75) 25%, rgba(23,22,22,0.85) 40%, rgba(23,22,22,0.9) 50%, rgba(36,41,38,0.85) 60%, rgba(48,58,52,0.75) 75%, rgba(62,79,70,0.65) 85%, rgba(23,22,22,0.3) 97%, rgba(23,22,22,0) 100%);font-size:large;margin:25.5% auto;width:100%;color:#fff;text-align:center;-webkit-animation:moveUp 2s forwards;animation:moveUp 2s forwards;transition:all 0.3s 0.3s}.notif h1{font-weight:bold;font-size:2rem}@-webkit-keyframes moveUp{from{opacity:0;-webkit-transform:none;transform:none}to{opacity:1;-webkit-transform:translateY(-50%);transform:translateY(-50%)}}@keyframes moveUp{from{opacity:0;-webkit-transform:none;transform:none}to{opacity:1;-webkit-transform:translateY(-50%);transform:translateY(-50%)}}.eareal-404{width:400px;top:55px;left:60%}@media screen and (max-width: 768px){.notif{background:#171616;background:linear-gradient(90deg, rgba(23,22,22,0) 0%, rgba(23,22,22,0.3) 7%, rgba(23,22,22,0.65) 15%, rgba(23,22,22,0.75) 25%, rgba(23,22,22,0.85) 40%, rgba(23,22,22,0.9) 50%, rgba(36,41,38,0.85) 60%, rgba(48,58,52,0.75) 75%, rgba(62,79,70,0.65) 85%, rgba(23,22,22,0.3) 97%, rgba(23,22,22,0) 100%);font-size:1.2rem;margin:25.5% auto;width:100%;color:#fff;text-align:center;-webkit-animation:moveUp 2s forwards;animation:moveUp 2s forwards;transition:all 0.3s 0.3s}.notif h1{font-weight:bold;font-size:0.7rem}}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}::-webkit-scrollbar{display:none}

